import {CSS_PREFIX} from "./Utils";

export const FONT_IMAGE = "data:image/png;base64,";

const BASE_CSS = `

/* ---------------- For CssScreen.ts ----------------- */

.trs80-emulator {
    display: inline-block;
    padding: 10px;
    background-color: #334843;
    border-radius: 8px;
    /* Pack the lines tightly: */
    line-height: 1px;
}

.trs80-emulator span {
    display: inline-block;
    white-space: pre;
    width: 8px;
    height: 24px;
    background-position: 0 0; /* Blank */
    background-repeat: no-repeat;
}

.trs80-emulator-expanded span {
    width: 16px;
}

.trs80-emulator-expanded .trs80-emulator-odd-column {
    display: none;
}

/* ---------------- For CanvasScreen.ts ----------------- */

.trs80-emulator-canvas-screen {
    display: inline-block;
    padding: 10px;
    background-color: #334843;
    border-radius: 8px;
}

/* ---------------- For FontScreen.ts ----------------- */

.trs80-emulator-font-screen {
    font-size: 24px;
    line-height: 24px;
    white-space: pre;
    display: inline-block;
    padding: 10px;
    background-color: #273833;
    color: #EEE;
    border-radius: 8px;
}

.trs80-emulator-font-screen-thumbnail {
    font-size: 8px;
    line-height: 8px;
    padding: 5px;
    border-radius: 4px;
}

.trs80-emulator-font-screen-narrow {
    font-family: "TreasureMIII64C";
}

.trs80-emulator-font-screen-expanded {
    font-family: "TreasureMIII32C";
}

.trs80-emulator-font-screen-expanded .trs80-emulator-font-screen-odd-column {
    display: none;
}

@font-face {
    /* http://www.kreativekorp.com/software/fonts/index.shtml#retro */
    font-family: "TreasureMIII64C";
    src: url("data:font/ttf;base64,");
}

@font-face {
    /* http://www.kreativekorp.com/software/fonts/index.shtml#retro */
    font-family: "TreasureMIII32C";
    src: url("data:font/ttf;base64,");
}

.trs80-emulator span {
    /* Generate with "base64 font.png". */
    background-image: url(` + FONT_IMAGE + `);
}
`;

/**
 * Make a global stylesheet for all TRS-80 emulators on this page. Idempotent.
 */
export function configureStylesheet(): void {
    const styleId = CSS_PREFIX + "-style";
    if (document.getElementById(styleId) !== null) {
        // Already created.
        return;
    }

    // Image is 512x480
    // 10 rows of glyphs, but last two are different page.
    // Use first 8 rows.
    // 32 chars across (32*8 = 256)
    // For thin font:
    //     256px wide.
    //     Chars are 8px wide (256/32 = 8)
    //     Chars are 24px high (480/2/10 = 24), with doubled rows.
    const lines: string[] = [];
    for (let ch = 0; ch < 256; ch++) {
        lines.push(`.${CSS_PREFIX}-narrow .${CSS_PREFIX}-char-${ch} { background-position: ${-(ch % 32) * 8}px ${-Math.floor(ch / 32) * 24}px; }`);
        lines.push(`.${CSS_PREFIX}-expanded .${CSS_PREFIX}-char-${ch} { background-position: ${-(ch % 32) * 16}px ${-Math.floor(ch / 32 + 10) * 24}px; }`);
    }

    const node = document.createElement("style");
    node.id = styleId;
    node.innerHTML = BASE_CSS + "\n\n" + lines.join("\n");
    document.head.appendChild(node);
}
